.p-inputswitch {
  .p-inputswitch-slider::before {
    transition-property: box-shadow transform;
    box-shadow: 0 3px 1px -2px rgba($overlayColor, 0.2), 0 2px 2px 0 rgba($overlayColor, 0.14),
      0 1px 5px 0 rgba($overlayColor, 0.12);
  }

  &:not(.p-disabled):hover {
    .p-inputswitch-slider::before {
      box-shadow: 0 3px 1px -2px rgba($overlayColor, 0.2), 0 2px 2px 0 rgba($overlayColor, 0.14),
        0 1px 5px 0 rgba($overlayColor, 0.12), 0 0 1px 10px rgba($overlayColor, 0.04);
    }
  }

  &.p-inputswitch-focus,
  &.p-inputswitch-focus:not(.p-disabled):hover {
    .p-inputswitch-slider::before {
      box-shadow: 0 0 1px 10px rgba($overlayColor, 0.12), 0 3px 1px -2px rgba($overlayColor, 0.2),
        0 2px 2px 0 rgba($overlayColor, 0.14), 0 1px 5px 0 rgba($overlayColor, 0.12);
    }
  }

  &.p-inputswitch-checked:not(.p-disabled):hover {
    .p-inputswitch-slider::before {
      box-shadow: 0 0 1px 10px rgba($inputSwitchHandleOnBg, 0.04), 0 3px 1px -2px rgba($overlayColor, 0.2),
        0 2px 2px 0 rgba($overlayColor, 0.14), 0 1px 5px 0 rgba($overlayColor, 0.12);
    }
  }

  &.p-inputswitch-checked.p-inputswitch-focus,
  &.p-inputswitch-checked.p-inputswitch-focus:not(.p-disabled):hover {
    .p-inputswitch-slider::before {
      box-shadow: 0 0 1px 10px rgba($inputSwitchHandleOnBg, 0.12), 0 3px 1px -2px rgba($overlayColor, 0.2),
        0 2px 2px 0 rgba($overlayColor, 0.14), 0 1px 5px 0 rgba($overlayColor, 0.12);
    }
  }
}
